import React from "react";
// import Musics from "./music.json";
// import Avatar from "@mui/material/Avatar";
// import Button from "@mui/material/Button";
// import IconButton from "@mui/material/IconButton";
import fs from "fs";
import List from "@mui/material/List";
import ListItemText from "@mui/material/ListItemText";
import Icons from "./icons";

export default function BasicCard() {
  const readFile = fs.readFileSync("src/music.json", "utf-8");
  const musicInfo = JSON.parse(readFile);

  return musicInfo.map((music, index) => {
    function track() {
      const items = [];
      for (var i = 0; i < music.track.length; i++) {
        items.push(
          <ListItemText className="List_text">{music.track[i]}</ListItemText>
        );
      }

      return <List>{items}</List>;
    }
    return (
      <div className="Card_layout" key={index}>
        <img className="Card_AlbumCover" src={music.img} alt="Harry's House" />
        <div className="Card_text">
          <p>{music.album}</p>
          <div className="Border_3"></div>
          <p>{music.artist}</p>
        </div>
        <p className="List_title">TRACK</p>
        <div className="List">{track()}</div>
        <div className="flex">
          <p className="date">{music.date}</p>
          <Icons />
        </div>
      </div>
    );
  });
}
